/* Roboto Slab */
// 300
@font-face {
  display: swap;
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 300;
  src: url('/src/fonts/roboto-slab-v34-cyrillic_latin-300.woff2')
      format('woff2'),
    url('/src/fonts/roboto-slab-v34-cyrillic_latin-300.woff') format('woff');
}

